.settings {
  background-color: inherit;
  height: 40px;
  border-style: none;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  font-family: inherit;
  line-height: 1.15;
  margin: 0;
  cursor: pointer;
}

.filter__icon {
  fill: #ff001b;
  margin-right: 20px;
}

.warning-notification {
  position: absolute;
  top: 40px;
  width: 280px;
  margin: 0 auto;
  font-size: 14px;
  line-height: 1.17;
  font-weight: 400;
  color: red;

  @media screen and (min-width: 768px) {
    width: 398px;
  }
  @media screen and (min-width: 1280px) {
    width: 398px;
  }
}

.wrong-text{
  font-size: 40px;
  display: flex;
  margin-right: 2px;
}